* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.cat-info {
  gap: 50px;
  margin-top: 20px;
  display: flex;
  position: relative;
}

.cat-photo {
  width: 400px;
  height: 100%;
  display: block;
}

.cat-title, .cat-temperament span {
  font-weight: bold;
}

.loader {
  width: 320px;
  height: 150px;
  box-sizing: border-box;
  z-index: 1;
  background: #fff;
  display: block;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader:after {
  content: "";
  width: calc(100% - 30px);
  height: calc(100% - 30px);
  box-sizing: border-box;
  background-image: linear-gradient(100deg, rgba(0, 0, 0, 0), rgba(255, 255, 255, .5) 50%, rgba(0, 0, 0, 0) 80%), linear-gradient(#ddd 56px, rgba(0, 0, 0, 0) 0), linear-gradient(#ddd 24px, rgba(0, 0, 0, 0) 0), linear-gradient(#ddd 18px, rgba(0, 0, 0, 0) 0), linear-gradient(#ddd 66px, rgba(0, 0, 0, 0) 0);
  background-position: 0 0, 0 0, 70px 5px, 70px 38px, 0 66px;
  background-repeat: no-repeat;
  background-size: 75px 130px, 55px 56px, 160px 30px, 260px 20px, 290px 56px;
  animation: animloader 1s linear infinite;
  position: absolute;
  top: 15px;
  left: 15px;
}

@keyframes animloader {
  0% {
    background-position: 0 0, 0 0, 70px 5px, 70px 38px, 0 66px;
  }

  100% {
    background-position: 150% 0, 0 0, 70px 5px, 70px 38px, 0 66px;
  }
}

